.default {
  margin: 10px;
  border: 1px solid transparent;
  border-radius: 4px;
  line-height: 1.75;
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: white;
  color: rgba(0, 0, 0, 0.65);
  border-color: #d9d9d9;
}
.default:focus {
  outline: none;
}
.default:disabled {
  cursor: not-allowed;
  color: rgba(0, 0, 0, 0.25);
  background-color: #f5f5f5;
  border-color: #d9d9d9;
}
.default:disabled:hover {
  color: rgba(0, 0, 0, 0.25);
  border-color: #d9d9d9;
  background-color: #f5f5f5;
}
.default:hover {
  color: #77adff;
  border-color: #77adff;
}
.default:active {
  color: #438fff;
  border-color: #438fff;
}
