.mainHeader {
  min-height: calc(100vh - 62px);
  background-position: center;
  background-size: cover;
  align-content: center;
  display: grid;
  position: relative;
}
.mainHeader::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #293a43;
  opacity: .8;
}
.headerContent {
  z-index: 1;
  color: white;
  text-align: center;
  padding: 40px 20px;
}
.headerContent h1 {
  font-size: 40px;
  line-height: 1.3;
  font-family: intro-creators, sans-serif;
  font-weight: normal;
  margin: 20px 0;
}
.accent {
  color: #5d9eff;
}
.companyName {
  display: block;
}
@media (min-width: 576px) and (max-width: 767px), (min-width: 768px) and (max-width: 991px) {
  .headerContent {
    padding: 60px 20px;
  }
  .headerContent h1 {
    font-size: 50px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .companyName {
    display: inline;
  }
}
@media (min-width: 992px) and (max-width: 1199px), (min-width: 1200px) {
  .headerContent {
    padding: 60px 20px;
  }
  .headerContent h1 {
    font-size: 60px;
    line-height: inherit;
  }
  .companyName {
    display: inline;
  }
}



