.landingSection {
  padding: 50px 0;
  background-color: #ededed;
}
.landingSection h2 {
  font-size: 26px;
  font-weight: 800;
  color: #4b5a65;
  line-height: 1.2;
  text-align: center;
}
.grid {
  display: grid;
  grid-template-columns: 1fr;
}
.sectionLeft {
  font-size: 18px;
  padding: 0 8%;
}
.sectionLeft h2 {
  margin-top: 0;
  margin-bottom: 40px;
}
.sectionRight {
  padding: 0 8%;
}
.imgResponsive {
  display: block;
  max-width: 100%;
  height: auto;
}
@media (min-width: 768px) and (max-width: 991px), (min-width: 992px) and (max-width: 1199px), (min-width: 1200px) {
  .landingSection {
    padding: 80px 0;
  }
  .landingSection h2 {
    font-size: 34px;
    text-align: left;
  }
  .grid {
    grid-template-columns: 1fr 1fr;
  }
  .sectionLeft {
    padding: 60px 60px 60px 0;
    border-right: 1px solid #dadada;
  }
  .sectionRight {
    padding: 0 0 0 40px;
  }
}



