.primary {
  margin: 10px;
  border: 1px solid transparent;
  border-radius: 4px;
  line-height: 1.75;
  cursor: pointer;
  transition: all 0.3s ease;
  color: white;
  border-color: #5d9eff;
  background-color: #5d9eff;
  display: inline-block;
  text-decoration: none;
}
.primary:focus {
  outline: none;
}
.primary:disabled {
  cursor: not-allowed;
  color: rgba(0, 0, 0, 0.25);
  background-color: #f5f5f5;
  border-color: #d9d9d9;
}
.primary:disabled:hover {
  color: rgba(0, 0, 0, 0.25);
  border-color: #d9d9d9;
  background-color: #f5f5f5;
}
.primary:hover {
  background-color: #77adff;
}
.primary:active {
  background-color: #438fff;
}
.large {
  padding: 10px 60px;
  font-size: 18px;
  font-weight: 800;
}
@media (min-width: 768px) and (max-width: 991px), (min-width: 992px) and (max-width: 1199px), (min-width: 1200px) {
  .largeSize {
    font-size: 20px;
  }
}



