.formSection {
  padding: 30px 0;
  background-color: #293a43;
}
.formSection input {
  border: none;
  box-sizing: border-box;
  padding: 15px 20px;
  width: 100%;
  font-family: Arial, sans-serif;
  font-style: italic;
  outline: none;
  transition: background-color 0.25s ease;
  background-color: #354a56;
  line-height: 1.15;
}
.formSection input:focus {
  background-color: #ededed;
}
.formSection input::placeholder {
  color: #6c8191;
}
.grid {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr;
}
.btn {
  font-size: 17px;
  margin: 0;
  height: 100%;
  padding: 14px;
  width: 100%;
  line-height: 1.15;
}
@media (min-width: 576px) and (max-width: 767px), (min-width: 768px) and (max-width: 991px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) and (max-width: 1199px), (min-width: 1200px) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}



