.header {
  width: 100%;
  z-index: 2;
  background: #fff;
  box-shadow: 0 2px 4px #f0f1f2;
}
.logo {
  display: grid;
  align-content: center;
}
.logo img {
  display: block;
  max-width: 100%;
  height: 20px;
}
.headerContent {
  height: 62px;
  padding: 10px;
  display: grid;
  grid-template-columns: auto 1fr;
}
.navigation {
  display: grid;
  align-content: center;
  text-align: right;
}
.navLinks {
  margin: 0;
  padding: 0;
}
.navLinks li {
  display: inline-block;
  padding: 0 18px 0 18px;
}
.navLinks li a {
  color: #73849a;
  text-decoration: none;
  font-weight: bold;
}



