.portfolio {
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
}
.portfolioItem {
  min-height: 32vh;
  background-size: cover;
  background-position: center;
  text-align: center;
  display: grid;
  align-content: center;
  padding: 20px;
  font-size: 20px;
  font-weight: 700;
  color: white;
  position: relative;
  transition: color .5s ease;
  cursor: pointer;
}
.portfolioItem:hover {
  color: #5d9eff;
}
.portfolioItem:hover:before {
  opacity: .85;
}
.portfolioItem::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #293a43;
  opacity: 0.6;
  transition: opacity .5s ease;
}
.portfolioItem > * {
  z-index: 1;
}
@media (min-width: 576px) and (max-width: 767px), (min-width: 768px) and (max-width: 991px) {
  .portfolio {
    grid-template-columns: repeat(3, 1fr);
  }
  .portfolioItem:nth-child(1) {
    grid-column: 1 / 3;
  }
  .portfolioItem:nth-child(4) {
    grid-column: 2 / 4;
  }
  .portfolioItem:nth-child(5) {
    grid-column: 1 / 3;
  }
}
@media (min-width: 992px) and (max-width: 1199px), (min-width: 1200px) {
  .portfolio {
    grid-template-columns: repeat(4, 1fr);
  }
  .portfolioItem:nth-child(1) {
    grid-column: 1 / 3;
  }
  .portfolioItem:nth-child(5) {
    grid-column: 2 / 4;
  }
  .portfolioItem:nth-child(6) {
    grid-column: 4;
    grid-row: 2 / 4;
  }
}



